// Importando o mdulo validator para validao de dados
import validator from "validator";

// Exportando a classe Contato como padro
export default class Contato {
  // Construtor da classe que recebe um seletor de classe de formulrio
  constructor(formClass) {
    this.form = document.querySelector(formClass);
  }

  // Mtodo de inicializao, invocado para configurar eventos
  init() {
    this.events();
  }

  // Mtodo para configurar eventos, chamado por init()
  events() {
    // Verifica se o formulrio existe antes de adicionar o evento de envio
    if (!this.form) return;
    
    // Adiciona um ouvinte de evento para o envio do formulrio
    this.form.addEventListener("submit", (e) => {
      // Impede o envio padro do formulrio
      e.preventDefault();
      
      // Chama o mtodo validate() para validar os campos do formulrio
      this.validate(e);
    });
  }

  // Mtodo para validar os campos do formulrio
  validate(e) {
    // Obtm o elemento HTML que acionou o evento (o formulrio)
    const el = e.target;

    // Obtm os elementos de input para nome, email e telefone dentro do formulrio
    const nomeInput = el.querySelector('input[name="nome"]');
    const emailInput = el.querySelector('input[name="email"]');
    const telefoneInput = el.querySelector('input[name="telefone"]');

    // Inicializa a varivel de erro como falso
    let error = false;

    // Verifica se o campo de nome est vazio
    if (!nomeInput.value) {
      // Se estiver vazio, exibe um alerta
      alert("Nome  um campo obrigatrio");
      // Define a varivel de erro como verdadeira
      error = true;
    }

    // Verifica se nem o campo de email nem o de telefone foram preenchidos
    if (!emailInput.value && !telefoneInput.value) {
      // Se ambos estiverem vazios, exibe um alerta
      alert("Um dos campos precisam ser preenchidos: email ou telefone");
      // Define a varivel de erro como verdadeira
      error = true;
    }

    // Se no houver erros, submete o formulrio
    if (!error) el.submit();
  }
}